/**
 * Module dependencies.
 */
import toSrtTime from './toSrtTime';
/**
 * Stringify the given array of captions.
 * @param {Array} captions
 * @return {String} srt
 */

export default function stringify(captions) {
  return captions.map(function (caption, index) {
    return (index > 0 ? '\n' : '') + [index + 1, "".concat(toSrtTime(caption.start), " --> ").concat(toSrtTime(caption.end)), caption.text].join('\n');
  }).join('\n') + '\n';
}